.container {
  display: flex;
  justify-content: space-between;
  padding: 2em 0 .5em;
  align-items: flex-end;
}

.container button {
  padding: 0.75em 1.5em;
  font-family: Ubuntu;
  border: none;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

.container .next_step {
  background-color: var(--marine);
  color: var(--white);
  border-radius: 0.5em;
  justify-self: left;
}

.container .confirm {
  background-color: var(--purplish);
}

.container .next_step:hover {
  background-color: var(--purplish);
}

.container .go_back {
  background-color: transparent;
  font-weight: 500;
  color: var(--cool);
}

.container .go_back:hover {
  color: var(--marine);
}

@media (max-width: 596px) {
  .container {
    position: absolute;
    bottom: 0;
    background-color: var(--white);
    width: 100%;
    align-items: center;
    padding: 1.5em;
  }
}